<template>
    <div class="member">
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>员工管理</el-breadcrumb-item>
            <el-breadcrumb-item>员工列表</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="search-people">
            <el-input
                    placeholder="请输入内容"
                    prefix-icon="el-icon-search"
                    size="small"
                    v-model="keyword">
            </el-input>
        </div>
        <div class="tile clearfix">
            <span class="ti bold">{{business?business:'未选择部门'}}</span>
            <el-button type="text"
                       style="color: #000;margin-top:4px;margin-left: 20px"
                       class="bold control-btn"
                       @click="editOrganize('edit',business)"
            >
                <i class="el-icon-edit"></i>
            </el-button>
            <el-button v-if="business" type="text" style="color: #000;margin-top:4px;margin-left: 20px" class="bold control-btn" @click="editOrganize('add',null)">添加子部门</el-button>
            <el-button v-if="business" class="fr" type="primary" style="margin-top:4px" @click="editWorker('add',null)">添加员工</el-button>
            <el-button class="fr bold control-btn"
                       type="text"
                       style="color: #000;margin-top:4px;margin-right: 20px"
                       @click="event.$emit('import-excel')"
            >批量导入</el-button>
        </div>
        <el-table
                :data="tableData"
                stripe
                height="620px"
        >
            <el-table-column
                    prop="checked"
                    width="50"
            >
                <template slot-scope="scope">
                    <el-checkbox v-model="scope.row.checked"></el-checkbox>
                </template>
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="员工姓名"
                    width="100"
            >
            </el-table-column>
            <el-table-column
                    prop="permission"
                    label="权限">
            </el-table-column>
            <el-table-column
                    prop="sex"
                    label="性别"
            >
            </el-table-column>
            <el-table-column
                    prop="birth"
                    label="生日"
            >
            </el-table-column>
            <el-table-column
                    prop="phone"
                    label="手机"
            >
            </el-table-column>
            <el-table-column
                    prop="control"
                    label="操作">
                <template slot-scope="scope">
                    <el-button type="text" size="small" class="member-control editmember" @click="editWorker('edit',null)">编辑</el-button>
                    <el-button type="text" size="small" class="member-control removemember" @click="deleteOne()">移除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="control">
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll">全选</el-checkbox>
            <el-button type="text" style="color: #000;margin-top:4px;margin-left: 20px" class="bold control-btn" @click="deleteSelect">移除</el-button>
            <el-button type="text" style="color: #000;margin-top:4px;margin-left: 20px" class=" fr bold control-btn">全部导出</el-button>
        </div>
        <EditWorker></EditWorker>
        <EditOrganize></EditOrganize>
        <ImportExcel></ImportExcel>
    </div>
</template>

<script>
    import EditWorker from '../components/member/edit-worker'
    import EditOrganize from '../components/member/edit-organize'
    import ImportExcel from '../components/member/import-excel'
    import { mapState } from 'vuex'
    export default {
        name: 'member',
        metaInfo: {
            title: '用户管理',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        components:{
            EditWorker,
            EditOrganize,
            ImportExcel
        },
        data() {
            return {
                keyword:'',
                tableData:[
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    },
                    {
                        checked:true,
                        name: '王老吉',
                        permission:'子管理员',
                        sex:'男',
                        birth:'1999-10-10',
                        phone: '13888888888'
                    }
                ]
            }
        },
        computed:{
            isIndeterminate(){
                let data = [];
                this.tableData.forEach(item=>{
                    if (item.checked){
                        data.push(item.checked)
                    }
                });
                if (data.length===0||data.length===this.tableData.length){
                    return false
                }else{
                    return true
                }
            },
            checkAll:{
                get(){
                    let data = [];
                    this.tableData.forEach(item=>{
                        if (item.checked){
                            data.push(item.checked)
                        }
                    });
                    if (data.length===this.tableData.length){
                        return true
                    }else{
                        return false
                    }
                },
                set(){
                    if (this.checkAll){
                        this.tableData.forEach((item,index)=>{
                            item.checked = false
                        })
                    }else{
                        this.tableData.forEach((item,index)=>{
                            item.checked = true
                        })
                    }
                }
            },
            ...mapState('memberList',['business'])
        },
        methods:{
            handleClick(val){
                this.event.$emit('edit-worker');
                console.log(val)
            },
            editWorker(type,id){
                let data = {type:type,id:id};
                this.$store.dispatch('user-dialog/initWorker',data)
            },
            editOrganize(type,name){
                let data = {type:type,name:name};
                this.$store.dispatch('user-dialog/initOrganize',data)
            },
            upload(){
                this.$notify({
                    title: '成功',
                    message: '导入成功',
                    type: 'success'
                });
            },
            deleteSelect() {
                this.$confirm('此操作将永久删除选中的员工, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            deleteOne() {
                this.$confirm('此操作将永久删除选中的员工, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '删除成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            }
        },
        mounted(){
            this.$store.dispatch('layout/SET_ASIDE','fault')
        },
        beforeDestroy(){
            this.$store.dispatch('layout/SET_ASIDE','default')
        }
    }
</script>

<style lang="stylus">
.member
    position relative
    .control-btn.el-button span:hover
        color #0090ff
    .search-people
        width 200px
        position absolute
        top -8px
        right 0
    .el-breadcrumb
        margin-bottom 20px
    .tile
        height 50px
        line-height 50px
        border-bottom #eaeaea 1px solid
        border-top #eaeaea 1px solid
        margin-bottom 10px
        span.ti
            color #0090ff
            font-size 18px
    .control
        position absolute
        bottom 0px
        height 40px
        width 910px
        padding 0 10px
    thead th
        padding 6px 0
        text-align center
        color white
        background-color #243444
        &.gutter
            background-color white
    tbody tr,tbody td
        padding 5px 0
    div.cell
        text-align center
    .el-table--striped .el-table__body tr.el-table__row--striped td
        background-color #d8ebfb
    .el-pagination
        text-align right
    .member-control
        color black
        &.editmember:hover
            color #0090ff
        &.removemember:hover
            color #f73c3c
    .el-dialog
        border-radius 6px
</style>
