<template>
    <div class="edit-worker">
        <el-dialog
            :title="worker.type"
            :visible.sync="worker.edit"
            :before-close="handleClose"
            width="730px"
        >
            <el-form
                :model="worker.form"
                style="width: 600px;margin: 0 auto"
            >
                <el-row :gutter="10">
                    <el-col :span="12">
                        <el-form-item label="员工姓名：" label-width="90px">
                            <el-input v-model="worker.form.name" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="员工ID：" label-width="90px">
                            <el-input v-model="worker.form.ID" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="10">
                    <el-col :span="12">
                        <el-form-item label="手机号码：" label-width="90px">
                            <el-input v-model="worker.form.phone" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="员工邮箱：" label-width="90px">
                            <el-input v-model="worker.form.mail" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="10">
                    <el-col :span="12">
                        <el-form-item label="员工性别：" label-width="90px">
                            <el-select v-model="worker.form.sex" placeholder="请选择">
                                <el-option
                                        v-for="item in worker.sexs"
                                        :key="item.val"
                                        :label="item.label"
                                        :value="item.val">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="入职时间：" label-width="90px">
                            <el-date-picker
                                    v-model="worker.form.entry"
                                    type="date"
                                    placeholder="选择日期"
                                    value-format="timestamp"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="10">
                    <el-col :span="12">
                        <el-form-item label="员工生日：" label-width="90px">
                            <el-date-picker
                                    v-model="worker.form.birth"
                                    type="date"
                                    placeholder="选择日期"
                                    value-format="timestamp"
                            >
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="员工职位：" label-width="90px">
                            <el-input v-model="worker.form.position" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="10">
                    <el-col :span="24">
                        <el-form-item label="所属部门：" label-width="90px">
                            <el-cascader
                                    style="width: 510px"
                                    :options="options"
                                    v-model="worker.form.oragnize"
                                    >
                            </el-cascader>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer">
                <el-button @click="close">取消</el-button>
                <el-button type="primary" @click="commitForm">确认</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { mapState } from "vuex"
    export default {
        name: 'edit-worker',
        data() {
            return {

            }
        },
        mounted(){
            // this.event.$on('edit-worker',function () {
            //     this.edit = true
            // }.bind(this))
        },
        methods:{
            handleClose(done) {
                this.$confirm('还未进行操作，确定关闭？')
                    .then(_ => {
                        done();
                    }).catch(_ => {});
            },
            commitForm(){
                console.log(this.worker.form);

            },
            close(){
                this.$store.dispatch('user-dialog/closeWorker')
            }
        },
        computed:{
            ...mapState('user-dialog',['options','worker'])
        }
    }
</script>

<style lang="stylus" scoped>
.edit-worker
    position relative
    .el-date-editor
        width 205px
</style>
