<template>
    <div class="import-excel">
        <el-dialog
            title="批量导入员工信息"
            :visible.sync="input"
            :before-close="handleClose"
            width="450px"
        >
            <div v-if="step===1" class="import-step ac import-step-1">
                <label for="uploadExcel" class="noSet cursor upload-excel ac">上传表格
                    <input id="uploadExcel" type="file" @change="selectFile" accept=".csv">
                </label>
                <p style="line-height: 30px;">请上传.csv格式的员工表格</p>
                <p style="line-height: 30px;">下载模板：<a style="color: #0090ff" href="/demo.csv">批量导入模板.csv</a></p>
            </div>
            <div v-if="step===2" class="import-step ac import-step-2">
                <img style="display: block" width="120" src="../../assets/member/excel.png" alt="">
                <p>{{fileName}}</p>
                <div style="padding-top: 20px">
                    <el-button type="primary" @click="upLoadFile">确认上传</el-button>
                </div>
            </div>
            <div v-if="step===3" class="import-step ac import-step-3">
                <img style="display: block" width="120" src="../../assets/member/excel.png" alt="">
                <p style="padding:12px;font-weight: bold">{{fileName}}</p>
                <div class="upload-err" style="height: 100px;width: 250px">
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                    <p>{{fileName}}</p>
                </div>
                <div style="padding-top: 20px">
                    <el-button type="primary" @click="upLoadFile">重新上传</el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name:'import-excel',
        data() {
            return {
                input:false,
                step:1,
                fileName:'员工通讯录.csv'
            }
        },
        mounted(){
            this.event.$on('import-excel',function () {
                this.input = true
            }.bind(this))
        },
        methods:{
            handleClose(done){
                this.$confirm('还未进行操作，确定关闭？')
                    .then(_ => {
                        this.step = 1;
                        done();
                    }).catch(_ => {});
            },
            selectFile(){
                this.fileName = window.document.getElementById('uploadExcel').files[0].name;
                this.step = 2
            },
            upLoadFile(){
                const loading = this.$loading({
                    lock: true,
                    text: '上传中',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });
                setTimeout(() => {
                    loading.close();
                    this.$notify({
                        title: '成功',
                        message: '导入成功',
                        type: 'success'
                    });
                    this.step = 3
                }, 2000);
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .import-excel
        position relative
        .import-step
            padding 20px 0
            min-height 150px
            display flex
            flex-direction column
            justify-content space-between
            img
                margin 0 auto
            .upload-err
                margin 0 auto
                overflow-y scroll
                p
                    color #F56C6C
        .upload-excel
            width 150px
            height 60px
            line-height 60px
            color white
            background-color #0090ff
            font-size 16px
            border-radius 6px
            margin 0 auto
            #uploadExcel
                display none
</style>
