<template>
    <div class="edit-organize">
        <el-dialog
            :title="title"
            :visible.sync="organize.edit"
            :before-close="handleClose"
            width="730px"
        >
            <el-form
                style="width: 600px;margin: 0 auto"
            >
                <el-form-item label="部门名称：" label-width="90px">
                    <el-input v-model="organize.name" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button v-if="confirm" @click="confirm = false">删除部门</el-button>
                <el-button v-else type="danger" @click="close()">确认删除</el-button>
                <el-button type="primary" @click="close()">添加</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    export default {
        name: 'edit-organize',
        data() {
            return {
                confirm:true,
            }
        },
        mounted(){

        },
        methods:{
            handleClose(done) {
                this.$confirm('还未进行操作，确定关闭？')
                    .then(_ => {
                        done();
                    }).catch(_ => {});
            },
            close(){
                this.confirm = true;
                this.$alert(`修改为了${this.organize.name}`, '修改结果', {
                    confirmButtonText: '确定',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
                this.$store.dispatch('user-dialog/closeOrganize')
            }
        },
        computed:{
            ...mapState('user-dialog',['options','organize']),
            title(){
                if (this.organize.type ==='add'){
                    return '新增子部门'
                } else{
                    return '编辑部门名称'
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
.edit-organize
    position relative
</style>
